import { Watch } from 'vue-property-decorator';

/*
 * @Author: Rybin 
 * @Date: 2018-01-12 14:12:36 
 * @Last Modified by: Rybin
 * @Last Modified time: 2018-01-12 20:49:03
 * @description: {} 
 */
import Vue from 'vue';
import Component from 'vue-class-component';
import { Action } from 'vuex-class';

// import Viewer from 'v-viewer';

import Api from '~/api';
import { ICriteriaVoInterface } from '~/entity/user';
import { EDUCATION_OPTIONS, ANNUAL_INCOME_TEXT_OPTIONS, ANNUAL_INCOME_OPTIONS, HOUSING_OPTIONS, IMG_HOST } from '~/config';
import { ToastMsg } from '~/entity/toast';
import { IViewImageOpt } from 'src/store/modules/view_image';
const incomes = ['不限'].concat(ANNUAL_INCOME_TEXT_OPTIONS.slice(1, ANNUAL_INCOME_TEXT_OPTIONS.length));

@Component({})
export default class ReceicedTaskIdeal extends Vue {
    params = null;
    // isViewing = false;
    criteriaVo: ICriteriaVoInterface = null;
    showIndex: number = null;

    imgHost = IMG_HOST;

    @Action('showToast')
    showToast;
    @Action('showLoading')
    showLoading;
    @Action('hideLoading')
    hideLoading;

    /* 新版大图预览 */
    @Action('updateViewImgs')
    updateViewImgs;

    @Action('toggleView')
    toggleView;

    get age() {
        if (this.criteriaVo) {
            const maxAge = this.criteriaVo.maxAge ? this.criteriaVo.maxAge : '不限';
            const minAge = this.criteriaVo.minAge ? this.criteriaVo.minAge : '不限';
            return this.getTxt(minAge, maxAge);
        } else {
            return '不限';
        }
    }

    get height() {
        if (this.criteriaVo) {
            const minHeight = this.criteriaVo.minHeight ? this.criteriaVo.minHeight : '不限';
            const maxHeight = this.criteriaVo.maxHeight ? this.criteriaVo.maxHeight : '不限';
            return this.getTxt(minHeight, maxHeight);
        } else {
            return '不限';
        }
    }

    get stature() {
        if (this.criteriaVo && this.criteriaVo.stature) {
            const stature = this.criteriaVo.stature !== '0' ? this.criteriaVo.stature : '不限';
            return stature;
        } else {
            return '不限';
        }
    }

    get education() {
        if (this.criteriaVo) {
            const options = EDUCATION_OPTIONS;
            let minEducation: any = this.criteriaVo.minEducation;
            let maxEducation: any = this.criteriaVo.maxEducation;
            minEducation = options[minEducation];
            maxEducation = options[maxEducation];
            return this.getTxt(minEducation, maxEducation);
        } else {
            return '不限';
        }
    }

    get income() {
        if (this.criteriaVo) {
            const options = incomes;
            let minIncome = incomes[ANNUAL_INCOME_OPTIONS.indexOf(this.criteriaVo.minIncome)];
            let maxIncome = incomes[ANNUAL_INCOME_OPTIONS.indexOf(this.criteriaVo.maxIncome)];
            return this.getTxt(minIncome, maxIncome);
        } else {    
            return '不限';
        }
    }

    get permanentPlace() {
        if (this.criteriaVo) {
            const permanentPlace = this.criteriaVo.permanentPlace !== '0' ? this.criteriaVo.permanentPlace : '不限';
            return permanentPlace;
        } else {
            return '不限';
        }
    }

    get company() {
        if (this.criteriaVo) {
            const company = this.criteriaVo.company !== '0' ? this.criteriaVo.company : '不限';
            return company;
        } else {
            return '';
        }
    }

    get civilState() {
        if (this.criteriaVo) {
            const civilState = this.criteriaVo.civilState !== '0' ? this.criteriaVo.civilState : '不限';
            return civilState;
        } else {
            return '不限';
        }
    }

    get birthPlace() {
        if (this.criteriaVo) {
            const birthPlace = this.criteriaVo.birthPlace !== '0' ? this.criteriaVo.birthPlace : '不限';
            return birthPlace;
        } else {
            return '不限';
        }
    }

    get housingCondition() {
        if (this.criteriaVo && this.criteriaVo.housingCondition) {
            const options = HOUSING_OPTIONS;
            return options[this.criteriaVo.housingCondition];
        } else {
            return '不限';
        }
    }
    
    get imgArray() {
        if (this.criteriaVo && this.criteriaVo.imgs !== null) {
            return this.criteriaVo.imgs;
        } else {
            return [];
        }
    }

    @Watch('criteriaVo.imgs')
    updateView() {
        const imgs = [];
        for (const photo of this.criteriaVo.imgs) {
            imgs.push(photo.url);
        }
        this.updateViewImgs(imgs);
    }

    activated() {
        // this.isViewing = false;
        this.params = this['$route'].params;
        this.getCirteria();
    }

    deactivated() {
        // if (this.isViewing) {
        //     const viewer = this.$refs['viewerInTask'].$viewer;
        //     viewer.hide();
        //     viewer.destroy();
        // }
        const opt: IViewImageOpt = {
            isShow: false,
            showIndex: 0
        };
        this.toggleView(opt);
    }

    getTxt(firTxt: string | number, secTxt: string | number) {
        if (firTxt === '不限' && secTxt === '不限') {
            return '不限';
        } else {
            return firTxt + ' 至 ' + secTxt;
        }
    }

    async getCirteria() {
        this.showLoading;
        try {
            const res = await Api.getTaskCriteria(this.params.taskId);
            this.hideLoading;
            this.criteriaVo = res.body.obj;
            console.log(this.criteriaVo);
        } catch (e) {
            this.hideLoading;
            this.showToast(new ToastMsg(e.message));
        }
    }

     /* 理想对象照片 */
     showImg(index: number) {
        // debugger;
        // this.showIndex = index;
        // const viewer = this.$refs['viewerInTask'].$viewer;
        // viewer.show();
        const opt: IViewImageOpt = {
            isShow: true,
            showIndex: index
        }; 
        this.toggleView(opt);
    }

    // viewShown() {
    //     console.log('viewShown');
    //     // debugger;
    //     this.isViewing = true;
    //     const viewer = this.$refs['viewerInTask'].$viewer; 
    //     viewer.view(this.showIndex);
    // }
}



